.header {
    position: fixed;
    /* position: absolute; */
    top: 0;
    left: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    transition: 0.6s;
    padding: 40px 100px;
    z-index: 1000;
  }
  
  .header.sticky {
    padding: 8px 100px !important;
    background: white;
  }

  .header.black{
    padding: 10px 100px !important;
    background: #262623;
  }
  
  .header .logo {
    position: relative;
    font-weight: 700;
    color: #fff;
    text-decoration: none;
    font-size: 2em;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: 0.6s;
    /* background: url("./favicon.png"); */
    /* background-size: cover; */
    width: 80px;
    height: 80px;
  }
  
  .image {
    width: 80px;
    background-blend-mode: soft-light !important;
  }
  .header ul {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .header ul li {
    position: relative;
    list-style: none;
  }
  
  .header ul li a {
    position: relative;
    margin: 0 8px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 500px;
    transition: 0.6s;
  }

  .header.sticky .logo {
    color: black;
  }
  
  .header.sticky ul li a {
    color: black;
  }

  .header.black .logo{
    color : white;
  }
   .header.black ul li a{
     color: white;
   }
